* {
  list-style: no;
  transition: all 0.5s;
}

li {
  list-style: none;
  margin: 0px;
  padding: 0px;
  text-align: start;
}

ul {
  padding: 0px;
}

.footer_container {
  background: rgb(237, 27, 36);
  color: white;
}

.navigation {
  background: rgb(237, 27, 36);
  height: 100px;
  color: white;
}
.navigation p {
  color: white;
}

#li {
  color: white;
  text-decoration: underline;
}

#li1 {
  text-decoration: underline;
}

.card1 {
  border: 2px solid black;
}

.card1:hover {
  border: 2px solid black;
  transform: scale(1.2);
}

.products {
  font-family: "Anek Malayalam", sans-serif;
  width: 100vw;
  background-color: rgba(237, 27, 36, 0.06);
}
.products .products1 {
  padding: 10px 0px;
}
.products .products1 .product {
  height: 550px;
  border: 2px solid rgba(237, 27, 36, 0.3);
  padding: 20px;
  margin: 30px 15px;
  background-color: white;
  color: black;
  border-radius: 10px;
  font-variant: small-caps;
}
.products .products1 .product .img {
  border-radius: 10px;
  border: 2px solid rgba(237, 27, 36, 0.3);
  width: 100%;
  height: 250px;
}
.products .products1 .product .img img {
  border-radius: 10px;
  width: 100%;
  height: 100%;
}
.products .products1 .product .name {
  font-size: 20px;
  font-weight: bold;
  margin: 10px 0px;
}
.products .products1 .product .disc {
  margin: 15px 0px;
  padding: 10px 0px;
}
.products .products1 .product .disc div {
  font-size: 15px;
  font-weight: bold;
  color: black;
}
.products .products1 .product .price {
  font-size: 20px;
  font-weight: bold;
  color: black;
  display: flex;
  justify-content: space-between;
  margin: auto;
}
.products .products1 .product .price .wholesale {
  font-size: 15px;
  font-weight: bold;
  color: white;
  background-color: green;
  padding: 5px;
}
.products .products1 .product .price .retail {
  padding: 5px;
  font-size: 15px;
  font-weight: bold;
  color: white;
  background-color: rgb(237, 27, 36);
}
.products .inquirybutton {
  color: white;
  background: rgb(237, 27, 36);
  margin-top: 10px;
  width: 100%;
  border: none;
  border-radius: 0%;
}
.products .inquiry {
  background: rgba(237, 27, 36, 0.8);
  color: white;
  display: none;
  justify-content: center;
  align-items: center;
  position: fixed;
  z-index: 1000;
  top: 0px;
  left: 0px;
  width: 100vw;
  height: 100vh;
}
.products .inquiry .closebutton {
  position: absolute;
  top: 0px;
  left: 0px;
  margin: 50px;
  font-size: 30px;
  color: white;
  cursor: pointer;
}

.singleproduct {
  background: rgba(237, 27, 36, 0.06);
  border-radius: 20px;
  display: flex;
  flex-direction: column;
}
.singleproduct .name,
.singleproduct .description,
.singleproduct .longdescription,
.singleproduct .price {
  margin: 10px;
}
.singleproduct .description,
.singleproduct .longdescription,
.singleproduct .wholesale,
.singleproduct .retail {
  font-size: 20px;
}
.singleproduct .name {
  width: 100%;
}
.singleproduct .img {
  border-radius: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 30px auto;
  width: 100%;
  border: 2px solid rgb(237, 27, 36);
  width: auto;
}
.singleproduct .img img {
  width: 50vh;
  height: 50vh;
  border-radius: 20px;
}
.singleproduct .price {
  text-align: center;
}
.singleproduct .price .wholesale {
  border-radius: 10px;
  font-weight: bold;
  color: white;
  background-color: green;
  padding: 15px;
  margin: 10px 0px;
}
.singleproduct .price .retail {
  border-radius: 10px;
  margin: 10px 0px;
  padding: 15px;
  font-weight: bold;
  color: white;
  background-color: rgb(237, 27, 36);
}

@media only screen and (max-width: 768px) {
  .singleproduct .img img {
    width: 100%;
    height: 100%;
  }
}
.form {
  width: 400px;
}

@media only screen and (max-width: 768px) {
  .form {
    width: 100%;
  }
}
.contactdetails .barcode {
  flex-wrap: wrap;
  justify-content: space-around;
}
.contactdetails .text {
  color: rgb(237, 27, 36);
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
}
.contactdetails .text1 h1 {
  font-size: 10em;
}

.middlecontact {
  transition: all 0.1s;
}
.middlecontact .container .container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.middlecontact .container .container a {
  text-decoration: none;
  color: black;
}
.middlecontact .container .container .col {
  margin: 40px;
  padding: 30px;
}
.middlecontact .container .container .reachus {
  box-shadow: 0px 1px 80px -45px rgba(0, 0, 0, 0.75);
  flex-direction: column;
  display: flex;
  align-items: center;
  justify-content: space-around;
  background-color: white;
}
.middlecontact .container .container .reachus .img {
  padding: 30px;
}
.middlecontact .container .container .reachus .img svg {
  fill: rgb(237, 27, 36);
}
.middlecontact .container .container .reachus h1 {
  padding: 1px;
  font-size: 35px;
}
.middlecontact .container .container .reachus h4 {
  padding: 1px;
  font-size: 20px;
}
.middlecontact .container .container .reachus:hover {
  background-color: #ed1b24;
  transform: scale(1.1);
}
.middlecontact .container .container .reachus:hover .img svg {
  fill: white;
}
.middlecontact .container .container .reachus:hover h1, .middlecontact .container .container .reachus:hover h4 {
  color: white;
}/*# sourceMappingURL=style.css.map */