$base_color: rgb(237, 27, 36);

* {
  list-style: no;
  transition: all 0.5s;
}
li {
  list-style: none;
  margin: 0px;
  padding: 0px;
  text-align: start;
}
ul {
  // border: 2px solid black;
  padding: 0px;
}
.footer_container {
  background: $base_color;
  color: white;
}
.navigation {
  background: $base_color;
  height: 100px;
  color: white;
  p {
    color: white;
  }
}

#li {
  color: white;
  text-decoration: underline;
}
#li1 {
  text-decoration: underline;
}
.card1 {
  border: 2px solid black;
}
.card1:hover {
  border: 2px solid black;
  transform: scale(1.2);
}

.products {
  font-family: "Anek Malayalam", sans-serif;
  width: 100vw;
  background-color: rgba($base_color, 0.06);
  .products1 {
    padding: 10px 0px;
    .product {
      height: 550px;
      border: 2px solid rgba($base_color, 0.3);
      padding: 20px;
      margin: 30px 15px;
      background-color: white;
      color: black;
      border-radius: 10px;
      .img {
        border-radius: 10px;
        border: 2px solid rgba($base_color, 0.3);
        width: 100%;
        height: 250px;
        img {
          border-radius: 10px;
          width: 100%;
          height: 100%;
        }
      }
      .name {
        font-size: 20px;
        font-weight: bold;
        margin: 10px 0px;
      }
      .disc {
        margin: 15px 0px;
        padding: 10px 0px;
        div {
          font-size: 15px;
          font-weight: bold;
          color: black;
        }
      }
      font-variant: small-caps;
      .price {
        font-size: 20px;
        font-weight: bold;
        color: black;
        display: flex;
        justify-content: space-between;
        .wholesale {
          font-size: 15px;
          font-weight: bold;
          color: white;
          background-color: green;
          padding: 5px;
        }
        .retail {
          padding: 5px;
          font-size: 15px;
          font-weight: bold;
          color: white;
          background-color: $base_color;
        }
        margin: auto;
      }
    }
  }
  .inquirybutton {
    color: white;
    background: $base_color;
    margin-top: 10px;
    width: 100%;
    border: none;
    border-radius: 0%;
  }
  .inquiry {
    background: rgba($base_color, 0.8);
    color: white;
    display: none;
    justify-content: center;
    align-items: center;
    position: fixed;
    z-index: 1000;
    top: 0px;
    left: 0px;
    width: 100vw;
    height: 100vh;
    .closebutton {
      position: absolute;
      top: 0px;
      left: 0px;
      margin: 50px;
      font-size: 30px;
      color: white;
      cursor: pointer;
    }
  }
}

.singleproduct {
  background: rgba($base_color, 0.06);
  border-radius: 20px;
  display: flex;
  flex-direction: column;
  .name,
  .description,
  .longdescription,
  .price {
    margin: 10px;
  }
  .description,
  .longdescription,
  .wholesale,
  .retail {
    font-size: 20px;
  }

  .name {
    width: 100%;
  }
  .img {
    border-radius: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 30px auto;
    width: 100%;
    border: 2px solid $base_color;
    width: auto;
    img {
      width: 50vh;
      height: 50vh;
      border-radius: 20px;
    }
  }
  .price {
    text-align: center;
    .wholesale {
      border-radius: 10px;
      font-weight: bold;
      color: white;
      background-color: green;
      padding: 15px;
      margin: 10px 0px;
    }
    .retail {
      border-radius: 10px;
      margin: 10px 0px;
      padding: 15px;
      font-weight: bold;
      color: white;
      background-color: $base_color;
    }
  }
}

@media only screen and (max-width: 768px) {
  .singleproduct {
    .img {
      img {
        width: 100%;
        height: 100%;
      }
    }
  }
}

.form {
  width: 400px;
}
@media only screen and (max-width: 768px) {
  .form {
    width: 100%;
  }
}

