.middlecontact {
  // background: rgba($base_color, 0.1);
  transition: all 0.1s;
  .container {
    .container {
      display: flex;
      align-items: center;
      a{
        text-decoration: none;
        color: black;
      }
      justify-content: space-between;
      .col {
        margin: 40px;
        padding: 30px;
      }
      .reachus {
        box-shadow: 0px 1px 80px -45px rgba(0, 0, 0, 0.75);
        flex-direction: column;
        display: flex;
        align-items: center;
        justify-content: space-around;
        background-color: white;
        .img {
          padding: 30px;
          svg {
            fill: $base_color;
          }
        }
        h1 {
          padding: 1px;
          font-size: 35px;
        }
        h4 {
          padding: 1px;
          font-size: 20px;
        }
      }
      .reachus:hover{
        background-color: rgba($base_color, 6);
        transform: scale(1.1);
        .img{
          svg{
            fill: white;
          }
        }
        h1, h4{
          color: white;
        }
      }
    }
  }
}

